import { Component, OnInit } from "@angular/core";

import { BatchService } from "../services/batch.service";
import { Router, ActivatedRoute } from "@angular/router";
import swal from "sweetalert2";
import { Batch } from "app/shared/models/batch.model";

@Component({
  selector: "app-edit-batch",
  templateUrl: "./edit-batch.component.html",
  styleUrls: ["./edit-batch.component.scss"]
})
export class EditbatchComponent implements OnInit {
  updatedbatch: Batch;
  batchid;

  constructor(
    public batchservice: BatchService,
    public router: Router,
    public activeRouter: ActivatedRoute
  ) {
    this.updatedbatch = {};

    this.batchid = this.activeRouter.snapshot.params["id"];
  }

  ngOnInit() {
    this.batchservice.getbatchById(this.batchid).subscribe(
      (data: any) => {
        console.log("batchlist>>", data);
        this.updatedbatch = data.data;
      },
      error => {
        console.log("cant fetch");
      }
    );
  }

  update() {
    console.log("Submitted>>>>>>>>>", this.updatedbatch);

    swal
      .fire({
        title: "Do you want to edit the batch details ?",
        text: "You won't be able to revert the change",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Confirm"
      })
      .then(result => {
        if (result.value) {
          //dev

          this.batchservice.editbatch(this.updatedbatch).subscribe(
            (data: any) => {
              swal.fire("Edit!", "Batch User has been edited.", "success");
              //dev
              console.log("Dataaa changeddd", data);
              this.ngOnInit();
            },
            error => {
              swal.fire("Not Changed!", "There was some error.", "error");
              //dev
              console.log("ERRORR", error);
            }
          );
        }
      });
  }
}
