import { Component, OnInit } from "@angular/core";
import { BatchService } from "../services/batch.service";
import { Router, ActivatedRoute } from "@angular/router";
import { Batch } from "../../../shared/models/batch.model";

import swal from "sweetalert2";

@Component({
  selector: "app-add-batch",
  templateUrl: "./add-batch.component.html",
  styleUrls: ["./add-batch.component.scss"]
})
export class AddBatchComponent implements OnInit {
  newbatch: Batch;
  submitting: boolean;
  constructor(
    public batchservice: BatchService,
    public router: Router,
    public activeRouter: ActivatedRoute
  ) {
    this.newbatch = {};
  }

  ngOnInit() {}

  submit() {
    console.log("Submitted>>>>>>>>>", this.newbatch);

    swal
      .fire({
        title: "Do you want to add the Applicant ?",
        text: "New user will be added in Applicant List",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Add"
      })
      .then(result => {
        if (result.value) {
          //dev

          this.batchservice.addBatch(this.newbatch).subscribe(
            (data: any) => {
              swal.fire("Add!", "Applicant has been added.", "success");
              //dev
              console.log("Dataaa changeddd", data);
            },
            error => {
              swal.fire("Not Added!", "There was some error.", "error");
              //dev
              console.log("ERRORR", error);
            }
          );
        }
      });
  }
}
