import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import { BatchService } from ".//services/batch.service";
import swal from "sweetalert2";
import { Batch } from "../../shared/models/batch.model";

@Component({
  selector: "app-batch",
  templateUrl: "./batch.component.html",
  styleUrls: ["./batch.component.scss"]
})
export class BatchComponent implements OnInit {
  batchList: Array<Batch>;

  constructor(public batchservice: BatchService, private router: Router) {
    this.batchList = [];
  }

  ngOnInit() {
    this.batchservice.getBatch().subscribe(
      (data: any) => {
        console.log("batchlist>>", data);
        this.batchList = data.data;
      },
      error => {
        console.log("cant fetch");
      }
    );
  }

  edit(id) {
    console.log("batch id>>", id);
    this.router.navigate(["/visa/batch/edit/" + id]);
  }

  delete(id) {
    swal
      .fire({
        title: "Do you want to delete the user ?",
        text: "You won't be able to revert the deleted user",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Delete"
      })
      .then(result => {
        if (result.value) {
          //dev

          this.batchservice.deleteBatch(id).subscribe(
            (data: any) => {
              swal.fire("Deleted!", "User has been deleted.", "success");
              //dev
              console.log("Dataaa changeddd", data);
              this.ngOnInit();
            },
            error => {
              swal.fire("Not Changed!", "There was some error.", "error");
              //dev
              console.log("ERRORR", error);
            }
          );
        }
      });
  }
}
